import React from 'react';
import styles from './style.module.sass';
import dataContent from '../../utils/dataContent';
import Modal from 'react-responsive-modal';
import Slider from "react-slick";
import '@fortawesome/fontawesome-free/css/all.min.css';
import '../../utils/slickSlider.sass';

function NextArrow(props) {
    const {onClick} = props;
    return (
        <button onClick={onClick} className={"slick-arrow slick-next"}><i className="fas fa-chevron-right"/></button>
    );
}

function PrevArrow(props) {
    const {onClick} = props;
    return (
        <button onClick={onClick} className={"slick-arrow slick-prev"}><i className="fas fa-chevron-left"/></button>
    );
}

class Stock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        }
    }

    onClickImg = () => {
        this.setState({isOpen: true});
    };

    closeModal = () => {
        this.setState({isOpen: false});
    };

    renderImages = () => {
        return dataContent.stockImages.map((el, i) =>
            <div key={i + "A"} ref={this.hamList}>
                <img src={require('../../public/images/' + el)} alt={el}/>
            </div>
        );
    };

    render() {
        const {isOpen} = this.state;
        const settings = {
            dots: true,
            infinite: true,
            speed: 300,
            slidesToShow: 1,
            slidesToScroll: 1,
            rows: 1,
            nextArrow: <NextArrow/>,
            prevArrow: <PrevArrow/>,
        };
        return (
            <>
                <Modal
                    closeIconSize={38} styles={{
                    closeButton: {
                      fill: "#ff28ed",
                    },
                    overlay: {
                        padding: '0'
                    },
                    modal: {
                        maxWidth: '100%',
                        background: 'none',
                        padding: '0',
                    }
                }} open={isOpen} onClose={this.closeModal} centered>
                    <Slider {...settings}>
                        {this.renderImages()}
                    </Slider>
                </Modal>
                <div className={styles.stock}>
                    <div className={styles.container}>
                        <div className={styles.text}>
                            {dataContent.titleStock}
                        </div>
                        <div className={styles.img}>
                            <img onClick={this.onClickImg}
                                 src={require("../../public/images/" + dataContent.stockImages[0])}
                                 alt={dataContent.stockImages[0]}/>
                            <img onClick={this.onClickImg}
                                 src={require("../../public/images/" + dataContent.stockImages[1])}
                                 alt={dataContent.stockImages[1]}/>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default Stock;